import React, { Component } from "react";
import Carousel from "react-elastic-carousel";
//import Logo from "./media01.jpg";

class App2 extends Component {
  state = {
    items: [
      {id:1 , text:'Subtitle 1', url: './media/media01.jpg'},
      {id:2 , text:'Subtitle 2', url: './media/media02.jpg'},
      {id:3 , text:'Subtitle 3', url: './media/media03.jpg'},
      {id:4 , text:'Subtitle 4', url: './media/media04.jpg'},
      {id:5 , text:'Subtitle 5', url: './media/media05.jpg'}
    ]
  }

  render() {
    const{ items } = this.state;
  return (
        <div>
        <Carousel>
        {items.map(item => <div key={item.id} > {item.text}<img src={item.url} alt={item.text} />   </div>)}
        </Carousel>
        </div>
  );
  }
}


 export default App2;
